<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('homeParty.caseTitle')"></mHeader>
    <div class="caseTop">
      <caseTop :head-top="$t('homeParty.caseTop')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
          </div>

          <div class="tabList">
            <div class="case1">
              <ModuleOne
                :titleDt="$t('homeParty.hititleDt')"
                :titleDd="$t('homeParty.hititleDd')"
                :titleSpan="$t('homeParty.hititleSpan')"
                h1="true"
              />
            </div>
            <div class="case2">
              <moduleTwo
                :twoP="$t('homeParty.hitwoP')"
                :twoSpan="$t('homeParty.hitwoSpan')"
                :twoSpan2="$t('homeParty.hitwoSpan2')"
                h2="true"
                h3="true"
              />
            </div>
            <div class="case3">
              <ModuleThree
                :threeP="$t('homeParty.hithreeP')"
                :threeSpan="$t('homeParty.hithreeSpan')"
                h4="true"
              />
            </div>
          </div>
        </van-tab>


        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <div class="case10">
              <moduleTen2
                homeInner="true"

                :tenDt1="$t('homeParty.hitenDt1')"
                :tenDd1="$t('homeParty.hitenDd1')"
                :tenDt2="$t('homeParty.hitenDt2')"
                :tenDd2="$t('homeParty.hitenDd2')"
                :tenDt3="$t('homeParty.hitenDt3')"
                :tenDd3="$t('homeParty.hitenDd3')"
                :tenDt4="$t('homeParty.hitenDt4')"
                :tenDd4="$t('homeParty.hitenDd4')"
                :tenDt5="$t('homeParty.hitenDt5')"
                :tenDd5="$t('homeParty.hitenDd5')"
                :tenDt6="$t('homeParty.hitenDt6')"
                :tenDd6="$t('homeParty.hitenDd6')"
                :tenDt7="$t('homeParty.hitenDt7')"
                :tenDd7="$t('homeParty.hitenDd7')"
              />
            </div>
          </div>
        </van-tab>

      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleOne from "../../../components/module/moduleOne.vue"
  import ModuleTwo from "../../../components/module/ModuleTwo.vue"
  import ModuleThree from "../../../components/module/ModuleThree.vue"
  import ModuleTen2 from "../../../components/module/ModuleTen2.vue"

  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0,
        listProcess:[{'dd':'JSS特供红色系花卉，打造西班牙式的热情。','dt':'花卉'},{'dd':'专业化妆师定制生日造型，做最美的寿星。','dt':'妆容'},{'dd':'JSS特供粉红色主题花卉，少女感十足。','dt':'花卉'},{'dd':'专业摄影师跟拍，捕捉精彩瞬间。另有摄像师全程记录并剪辑，根据需求制作不同风格VCR。','dt':'记录'},{'dd':'VR游戏、世界百大DJ主持派对、惊喜生日短片。','dt':'娱乐'},{'dd':'邀请函设计、晚宴菜单设计及制作。','dt':'物料设计'}]
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        //console.log(scrollTop);
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleOne,
      ModuleTwo,
      ModuleThree,
      ModuleTen2
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      .caseVantTab{
        height: 100%;
        .tabList{
          background: #fff;
          padding-top: 2rem;
          .case1{
            margin-bottom: 1rem;
          }
          .case2{
            margin-bottom: 1.2rem;
          }
          .case3{
            padding-bottom: .8rem;
          }
        }
        .tabList2{
          background: #fff;
          .case10{
            padding-bottom: .8rem;
            padding-top: 1.82rem;
          }
        }
      }
    }
  }

</style>



